import React from 'react';

class PlayerInfo extends React.Component {
	constructor(props) {
    super(props);
	}
	render() {
		if (!this.props.username) 
			return (
				<div className="playerInfo">
					<div className="infoMessage">No player selected.</div>
				</div>
			)
		return (
			<div className="playerInfo">
				<div className="playerProfile">
					<img src="avatar.gif" alt="Avatar"/>
					<span className="playerName">{this.props.username}</span>
					<div className="playerTiers">Tiers played</div>
					<div className="playerRecord">Record</div>
				</div>
				<div className="playerUsage"></div>
				<div className="playerStats"></div>
			</div>
		)
	}
}

export default PlayerInfo;


