import React, { Component } from 'react';
import './App.css';

import players from './data/players.csv'
import csv from 'csv';

import PlayerList from './components/PlayerList';
import PlayerInfo from './components/PlayerInfo';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      players: [],
      username: ""
    }
  }
  componentDidMount() {
    fetch(players)
      .then(res => res.text()
      .then(text => this.setState({players:text.split('\n')})))
  }
  handleClick = (username) => {
    this.setState({username});
    // expand
  }
  render() {
    return (
      <div className="App">
        <PlayerList 
          players={this.state.players}
          onClick={this.handleClick}
        />
        <PlayerInfo username={this.state.username}/>
      </div>
    );
  }
}

export default App;



